/************************/
export  const getImg = (params) =>{
  return axios({
    url: '/admin/getImg',
    method: 'get',
    headers: {
      "appid": "1"
    },
    params
  })
}
//
export  const uploadImg = (data) =>{
  return axios({
    url: '/admin/uploadImg',
    method: 'post',
    headers: {
      "Content-Type": "multipart/form-data",
      "appid": "1"
    },
    data
  })
}
//
export  const deleteImg = (data) =>{
  return axios({
    url: '/admin/deleteImg',
    method: 'post',
    headers: {
      "appid": "1"
    },
    data
  })
}

