import axios from '../axios'

/*
 * 
 */

// 
export const login = data => {
    data = JSON.stringify(data)
    return axios({
        url: '/login',
        method: 'post',
        data
    })
}

// 
export const logout = () => {
    return axios({
        url: '/logout',
        method: 'get'
    })
}



