import Mock from 'mockjs'
import { baseUrl } from '@/utils/global'
import * as login from './modules/login'
import * as user from './modules/user'
import * as role from './modules/role'
import * as dept from './modules/dept'
import * as menu from './modules/menu'
import * as dict from './modules/dict'
import * as log from './modules/log'

// 1. /[], [openMock].
// 2. /[], fnCreate[isOpen].
// 3. /[], [isOpen].
let openMock = true
// let openMock = false
fnCreate(login, false)
fnCreate(user, false)
fnCreate(role, false)
fnCreate(dept, false)
fnCreate(menu, false)
fnCreate(dict, false)
fnCreate(log, false)

/**
 * mock
 * @param {*} mod 
 * @param {*} isOpen ?
 */
function fnCreate (mod, isOpen = true) {
  
  if (isOpen) {
    for (var key in mod) {
      ((res) => {
        if (res.isOpen !== false) {
          let url = baseUrl
          if(!url.endsWith("/")) {
            url = url + "/"
          }
          url = url + res.url
          Mock.mock(new RegExp(url), res.type, (opts) => {
            opts['data'] = opts.body ? JSON.parse(opts.body) : null
            delete opts.body
            console.log('\n')
            console.log('%cmock, : ', 'color:blue', opts)
            console.log('%cmock, : ', 'color:blue', res.data)
            return res.data
          })
        }
      })(mod[key]() || {})
    }
  }
}



