import Vue from 'vue'
import Router from 'vue-router'
import Cookies from "js-cookie"
import Login from '@/views/Login'
import NotFound from '@/views/Error/404'
import Home from '@/views/Home'
import Intro from '@/views/Home/Intro'
import api from '@/http/api'
import store from '@/store'
import { getIFramePath, getIFrameUrl } from '@/utils/iframe'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: '',
      component: Home,
      children: [
        {
          path: '',
          name: '',
          component: Intro,
          meta: {
            icon: 'fa fa-home fa-lg',
            index: 0
          }
        }
      ]
    },
    {
      path: '/login',
      name: '',
      component: Login
    },
    {
      path: '/404',
      name: 'notFound',
      component: NotFound
    }
  ]
})

router.beforeEach((to, from, next) => {
  // 
  // 
  let token = sessionStorage.getItem('token')
  let userName = sessionStorage.getItem('user')
  if (to.path === '/login') {
    // 
    if(token) {
      next({ path: '/' })
    } else {
      next()
    }
  } else {
    if (!token) {
      // 
      next({ path: '/login' })
    } else {
      // 
      addDynamicMenuAndRoutes(userName, to, from)
      next()
    }
  }
})

/**
* 
*/
function addDynamicMenuAndRoutes(userName, to, from) {
  // IFrame
  handleIFrameUrl(to.path)
  if(store.state.app.menuRouteLoaded) {
    console.log('.')
    return
  }
  api.menu.findNavTree({'username':userName})
  .then(res => {
    // 
    let dynamicRoutes = addDynamicRoutes(res.data)
    console.log(dynamicRoutes)
    // 
    handleStaticComponent(router, dynamicRoutes)
    router.addRoutes(router.options.routes)
    // 
    store.commit('menuRouteLoaded', true)
    // 
    store.commit('setNavTree', res.data)
  }).then(res => {
    api.user.findPermissions({'username':userName}).then(res => {
      // 
      store.commit('setPerms', res.data)
    })
  })
  .catch(function(res) {
  })
}

/**
 * 
 * '''Generator'
 */
function handleStaticComponent(router, dynamicRoutes) {
  for(let j=0;j<dynamicRoutes.length; j++) {
    if(dynamicRoutes[j].name == '') {
      dynamicRoutes[j].component = Generator
      break
    }
  }
  router.options.routes[0].children = router.options.routes[0].children.concat(dynamicRoutes)
}

/**
 * IFrame
 */
function handleIFrameUrl(path) {
  // iframeUrlstoreIFrame
  let url = path
  let length = store.state.iframe.iframeUrls.length
  for(let i=0; i<length; i++) {
    let iframe = store.state.iframe.iframeUrls[i]
    if(path != null && path.endsWith(iframe.path)) {
      url = iframe.url
      store.commit('setIFrameUrl', url)
      break
    }
  }
}

/**
* ()
* @param {*} menuList 
* @param {*} routes ()
*/
function addDynamicRoutes (menuList = [], routes = []) {
 var temp = []
 for (var i = 0; i < menuList.length; i++) {
   if (menuList[i].children && menuList[i].children.length >= 1) {
     temp = temp.concat(menuList[i].children)
   } else if (menuList[i].url && /\S/.test(menuList[i].url)) {
      menuList[i].url = menuList[i].url.replace(/^\//, '')
      // 
      var route = {
        path: menuList[i].url,
        component: menuList[i].component,
        name: menuList[i].name,
        meta: {
          icon: menuList[i].icon,
          index: menuList[i].id
        }
      }
      let path = getIFramePath(menuList[i].url)
      if (path) {
        // , iframe
        route['path'] = path
        route['component'] = resolve => require([`@/views/IFrame/IFrame`], resolve)
        // URL
        let url = getIFrameUrl(menuList[i].url)
        let iFrameUrl = {'path':path, 'url':url}
        store.commit('addIFrameUrl', iFrameUrl)
      } else {
       try {
         // URLvuevueurl
         // url="sys/user""@/views/sys/user.vue",
         let array = menuList[i].url.split('/')
         let url = ''
         for(let i=0; i<array.length; i++) {
            url += array[i].substring(0,1).toUpperCase() + array[i].substring(1) + '/'
         }
         url = url.substring(0, url.length - 1)
         route['component'] = resolve => require([`@/views/${url}`], resolve)
       } catch (e) {}
     }
     routes.push(route)
   }
 }
 if (temp.length >= 1) {
   addDynamicRoutes(temp, routes)
 } else {
   console.log('...')
   console.log(routes)
   console.log('.')
 }
 return routes
}

export default router



