<template>
  <div class="container">
      <!--  -->
      <nav-bar></nav-bar>
      <!--  -->
      <head-bar></head-bar>
      <!--  -->
      <main-content></main-content>
  </div>
</template>

<script>
import HeadBar from "./homeLayout/HeadBar"
import NavBar from "./homeLayout/NavBar"
import MainContent from "./homeLayout/MainContent"
export default {
  components:{
        HeadBar,
        NavBar,
        MainContent
  }
};
</script>

<style scoped lang="scss">
  .container {
    position:absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    // background: rgba(224, 234, 235, 0.1);
  }
</style>



