import axios from '../axios'

/*
 * 
 */

 // 
export const save = (data) => {
    return axios({
        url: '/system/menu/save',
        method: 'post',
        data
    })
}
// 
export const batchDelete = (data) => {
    return axios({
        url: '/system/menu/delete',
        method: 'post',
        data
    })
}
// 
export const findNavTree = (params) => {
    return axios({
        url: '/system/menu/getNavTree',
        method: 'get',
        params
    })
}



