import axios from '../axios'

/*
 * 
 */

// 
export const save = (data) => {
    return axios({
        url: '/system/dictionaries/save',
        method: 'post',
        data
    })
}
// 
export const batchDelete = (data) => {
    return axios({
        url: '/system/dictionaries/delete',
        method: 'post',
        data
    })
}
// 
export const findPage = (params) => {
    return axios({
        url: '/system/dictionaries/list',
        method: 'get',
        params
    })
}



